/* src/CSS/Login.css - Minimalist & Modern */

/* พื้นหลังหลัก */
.login-main {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f7f9fc; /* สีเทาอ่อนมาก */
  padding: 20px;
}

/* กล่อง Login */
.login-container {
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05); /* ลดเงาให้ดูบางเบา */
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 800px;
  overflow: hidden;
}

@media (min-width: 768px) {
  .login-container {
    flex-direction: row;
  }
}

/* ส่วนฟอร์ม */
.form-wrapper {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form-header {
  display: flex;
  margin-bottom: 25px;
  border-bottom: 1px solid #eee;
}

.form-header button {
  flex: 1;
  padding: 12px 0;
  font-size: 1em;
  font-weight: 500;
  color: #999;
  transition: all 0.3s ease;
  position: relative;
  background: none;
  border: none;
  cursor: pointer;
}

.form-header button.active {
  color: #212121;
}

.form-header button.active::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #55a67c;
}

.login-form h2 {
  font-size: 1.8em;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
}

.form-group {
  margin-bottom: 18px;
}

.form-group label {
  display: block;
  margin-bottom: 6px;
  font-weight: 500;
  color: #666;
}

.form-group input {
  width: 100%;
  padding: 12px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1em;
  transition: border-color 0.3s ease;
}

.form-group input:focus {
  outline: none;
  border-color: #E8F2E9; /* ใช้สีหลัก E8F2E9 */
}

.submit-button {
  width: 100%;
  padding: 14px;
  background-color: #E8F2E9; /* ใช้สีหลัก E8F2E9 */
  color: #333;
  border-radius: 8px;
  font-size: 1.1em;
  font-weight: 600;
  margin-top: 20px;
  transition: background-color 0.3s ease, transform 0.2s ease;
  border: none;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #d1e4d1;
  transform: translateY(-1px);
}

.submit-button:active {
  transform: translateY(0);
}

/* เส้นแบ่ง (ในมือถือ) */
.divider {
  display: flex;
  align-items: center;
  text-align: center;
  margin: 25px 0;
  color: #ccc;
  font-size: 0.9em;
}

.divider::before,
.divider::after {
  content: '';
  flex: 1;
  border-bottom: 1px solid #eee;
}

.divider:not(:empty)::before {
  margin-right: .25em;
}

.divider:not(:empty)::after {
  margin-left: .25em;
}

@media (min-width: 768px) {
  .divider {
    flex: 0 0 1px;
    width: 1px;
    height: 100%;
    background-color: #eee;
    margin: 0 40px;
    flex-direction: column;
  }

  .divider span {
    background-color: #fff;
    padding: 10px 0;
    position: relative;
    top: -10px;
  }

  .divider::before,
  .divider::after {
    border-bottom: none;
    border-left: 1px solid #eee;
    height: 50%;
    width: 1px;
  }
  .divider:not(:empty)::before { margin-right: 0; margin-bottom: .25em; }
  .divider:not(:empty)::after { margin-left: 0; margin-top: .25em; }
}

/* ส่วน Google Login */
.google-login {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fafafa;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}

@media (max-width: 767px) {
  .google-login {
    border-radius: 0 0 12px 12px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}

.google-login h2 {
    font-size: 1.6em;
    margin-bottom: 20px;
    color: #333;
    text-align: center;
}

.google-login-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 280px;
  padding: 12px 20px;
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1em;
  font-weight: 500;
  color: #555;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.google-login-button img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.google-login-button:hover {
  background-color: #fcfcfc;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
}

.google-login-button:active {
  background-color: #f5f5f5;
}